import React, { useState, useEffect } from 'react';
import { Input } from 'antd';
import { db } from '../config';
import { collection, query, getDocs } from 'firebase/firestore';
import { toast } from 'react-toastify';
import Ketqua from './Ketqua';
import Loading from './Loading';

function Tracuu() {
    const [cmnd, setCmnd] = useState('');
    const [phone, setPhone] = useState('');
    const [showAlert, setShowAlert] = useState(false);
    const [cvData, setCvData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [matchedCVs, setMatchedCVs] = useState([]);
    const [showForm, setShowForm] = useState(true);

    const validatePhone = (phone) => {
        const phoneRegex = /^0\d{9}$/;
        return phoneRegex.test(phone);
    };

    const validateCmnd = (cmnd) => {
        const cmndRegex = /^\d{9,12}$/;
        return cmndRegex.test(cmnd);
    };

    useEffect(() => {
        const fetchCVData = async () => {
            try {
                const q = query(collection(db, 'CV'));
                const querySnapshot = await getDocs(q);
                const data = querySnapshot.docs.map(doc => ({
                    id: doc.id,
                    ...doc.data()
                }));
                setCvData(data);
                setLoading(false);
            } catch (err) {
                console.error("Error fetching CV data: ", err);
                setError("An error occurred while fetching data");
                setLoading(false);
            }
        };

        fetchCVData();
    }, []);

    const notify = () => {
        if (!cmnd || !phone) {
            toast.error('Anh/chị vui lòng nhập cả số CMND/CCCD và Số điện thoại để tra cứu kết quả ứng tuyển', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return;
        }

        if (!validateCmnd(cmnd)) {
            toast.error('Số CMND/CCCD không hợp lệ, vui lòng kiểm tra lại', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return;
        }

        if (!validatePhone(phone)) {
            toast.error('Số điện thoại không hợp lệ, vui lòng kiểm tra lại', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return;
        }

        const matchedCVs = cvData.filter(cv => (cv.CMND === cmnd) && (cv.SoDienThoai === phone));

        if (matchedCVs.length > 0) {
            const sortedMatchedCVs = matchedCVs.sort((a, b) => {
                const dateA = new Date(a.NgayGuiCV.split('/').reverse().join('-'));
                const dateB = new Date(b.NgayGuiCV.split('/').reverse().join('-'));
                return dateB - dateA;
            });
            setMatchedCVs(sortedMatchedCVs);
            setShowAlert(false);
            setShowForm(false);
        } else {
            setMatchedCVs([]);
            setShowAlert(true);
        }
    };

    const handleBack = () => {
        setShowForm(true);
        setMatchedCVs([]);
        setCmnd('');
        setPhone('');
        setShowAlert(false);
    };

    if (loading) return <div><Loading /></div>;
    if (error) return <div>Error: {error}</div>;

    return (
        <div className="job-result__container">
            {showForm ? (
                <div className="job-result__wapper">
                    <div className="job-result-search__header">
                        <h1 className="job-result__title">Tra cứu kết quả ứng tuyển</h1>
                        <div className="job-result__sub-title">Anh/chị vui lòng nhập cả số CMND/CCCD và Số điện thoại để tra cứu kết quả ứng tuyển</div>
                    </div>
                    <div className="job-result-search__form">
                        <div className="job-result-form__control col-12 p-0">
                            <div className="input-title">CMND/CCCD</div>
                            <Input
                                placeholder="Nhập số CMND/CCCD"
                                autoComplete="off"
                                className="job-result-input"
                                size='large'
                                value={cmnd}
                                onChange={(e) => setCmnd(e.target.value)}
                            />
                        </div>
                        <div className="job-result-form__control col-12 p-0">
                            <div className="input-title">Số điện thoại</div>
                            <Input
                                placeholder="Nhập số điện thoại"
                                autoComplete="off"
                                className="job-result-input"
                                size='large'
                                value={phone}
                                onChange={(e) => setPhone(e.target.value)}
                            />
                        </div>
                        {showAlert && (
                            <div className="job-result-form__error">
                                <span className="mr-2">
                                    <svg width="24px" height="24px" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M12 2C6.5 2 2 6.5 2 12C2 17.5 6.5 22 12 22C17.5 22 22 17.5 22 12C22 6.5 17.5 2 12 2ZM13 16C13 16.6 12.6 17 12 17C11.4 17 11 16.6 11 16V12C11 11.4 11.4 11 12 11C12.6 11 13 11.4 13 12V16ZM12 9C11.4 9 11 8.6 11 8C11 7.4 11.4 7 12 7C12.6 7 13 7.4 13 8C13 8.6 12.6 9 12 9Z" fill="#FC625D" />
                                    </svg>
                                </span>
                                <span>Không tìm thấy kết quả ứng tuyển. Anh/chị vui lòng kiểm tra lại thông tin đã nhập.</span>
                            </div>
                        )}
                        <div className="job-result-form__btn-submit" onClick={notify}>Tra cứu kết quả</div>
                    </div>
                </div>
            ) : (
                <Ketqua cvs={matchedCVs} onBack={handleBack} />
            )}
        </div>
    );
}

export default Tracuu;